:root{
    --sidebar-flex:1.5;
    --condiv-flex:6;
    --fontsize-p:1.2rem;
    --fontsize-nav:1.6rem;
    --typing-font:3rem;
    }
    *
    {
    margin:0;
    padding: 0;
    }

    .App
    {
    display: -webkit-flex;
    display: flex;
    width: 100vw;
    min-height: 100vh;
  
    }
    /* .condiv class is for the right side content container*/
    .condiv
    {
    padding: 100px ;
    background-color: #f4f5f9;
    -webkit-flex:6;
            flex:6;
    -webkit-flex:var(--condiv-flex);
            flex:var(--condiv-flex);
    }
nav
{
padding:40px 30px 0px 30px ;
-webkit-flex:1.5;
