body {


    background-image:url(/static/media/background2.8736614b.jpg);
    
    padding: 20px;
    margin: 0;
    font-family: 'Raleway';
  }
  h1, h2, p, ul, li {
    font-family: sans-serif;
  }
  ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
  }



  .x {
    display:inline-block;
    padding:15px;
    margin:0 0.3em 0.3em 0;
    border-radius:2em;
    padding-left: 10px;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    font-weight:300;
    color:#0c0c0c;
    background-color:#37e1de;
    text-align:center;
    transition: all 0.2s;
      
  }

  .mailto
  {
    display:inline-block;
    padding:15px;
    margin:0 0.3em 0.3em 0;
    border-radius:2em;
    padding-left: 10px;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    font-weight:300;
    color:#0c0c0c;
    background-color:#37e1de;
    text-align:center;
    transition: all 0.2s;

  }

  .x:hover{

    
    color:#948d8d;
    border-color:#393838;
      
  }

  .mailto:hover{

    
    color:#948d8d;
    border-color:#393838;
      
  }



  ul.header li a {
    color: rgb(0, 0, 0);
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
  }
  .content {
    background-color: #FFF;
    padding: 20px;
  }
  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
  }

  



